import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {debounceTime,map } from "rxjs/operators";
import {Observable} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  apiURLAutoComplete: string='http://responsive-env.eba-9bzm8k2k.us-east-1.elasticbeanstalk.com/autocomplete/';
  apiDetails: string='http://responsive-env.eba-9bzm8k2k.us-east-1.elasticbeanstalk.com/details/'
  apidescriptionDetails: string='http://responsive-env.eba-9bzm8k2k.us-east-1.elasticbeanstalk.com/details/description/'
  apinewsDetails: string='http://responsive-env.eba-9bzm8k2k.us-east-1.elasticbeanstalk.com/details/news/'
  private apisummaryChartsDetails: string='http://responsive-env.eba-9bzm8k2k.us-east-1.elasticbeanstalk.com/details/charts/';
  // private apisummaryChartsDetails: string='http://localhost:8081/details/charts/';

  private apiChartsDetails: string='http://responsive-env.eba-9bzm8k2k.us-east-1.elasticbeanstalk.com/details/smachart/';

  constructor(private httpClient: HttpClient) { }
  trialmethod()
  {
    return console.log('trial success');
  }
  getAutoCompleteResults(keyCharacter: string) {
    console.log("entered service")
    var response = this.httpClient.get(this.apiURLAutoComplete + keyCharacter)
      .pipe(
        debounceTime(400),
        map(
          (data: any) => {
            return (
              data != [] ? data as any[] : [{"error": "Invalid key"} as any]
            );
          }
        )
      );
    return response;
  }
  getTickDetails(keyCharacter: string):Observable<any> {
    console.log("entered service")
    var response = this.httpClient.get(this.apiDetails + keyCharacter)
      .pipe(
        debounceTime(400),
        map(
          (data: any) => {
            return (
              data != [] ? data as any[] : [{"error": "Invalid key"} as any]
            );
          }
        )
      );
    return response;
  }


  getDescriptionDetails(ticker: string):Observable<any> {
    console.log("entered descriptionservice")
    var response = this.httpClient.get(this.apidescriptionDetails + ticker)
      .pipe(
        debounceTime(400),
        map(
          (data: any) => {
            return (
              data != [] ? data as any[] : [{"error": "Invalid key"} as any]
            );
          }
        )
      );
    return response;
  }

  getNewsDetails(ticker: string) :Observable<any>{
    console.log("entered service")
    var response = this.httpClient.get(this.apinewsDetails + ticker)
      .pipe(
        debounceTime(400),
        map(
          (data: any) => {
            return (
              data != [] ? data as any[] : [{"error": "Invalid key"} as any]
            );
          }
        )
      );
    return response;

  }

  getChartDetails(ticker: string) :Observable<any>{
    console.log("entered service")
    var response = this.httpClient.get(this.apiChartsDetails + ticker)
      .pipe(
        debounceTime(400),
        map(
          (data: any) => {
            return (
              data != [] ? data as any[] : [{"error": "Invalid key"} as any]
            );
          }
        )
      );
    return response;

  }

  getsummaryChartDetails(ticker: string,lastdate:any) :Observable<any>{
    console.log("entered service")
    var response = this.httpClient.get(this.apisummaryChartsDetails + ticker+'/'+lastdate)
      .pipe(
        debounceTime(400),
        map(
          (data: any) => {
            return (
              data != [] ? data as any[] : [{"error": "Invalid key"} as any]
            );
          }
        )
      );
    return response;

  }





}
